/*    QUIZ*/
* {
    box-sizing: border-box;
}

.quiz {

    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.question {
    font-size: 1em;
}

.quiz-container {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 2px rgba(100, 100, 100, 0.1);
    width: 100%;
    height: 420px;
    margin-left: auto;
    margin-right: auto;
}

#quiz2-img1,
#quiz2-img2 {
    border-radius: 10px;
    width: 60%;
    /*height: 200px;*/
    margin-top: 4%;
}


.quiz-header {
    padding: 5%;
    height: 90%;
}

.quiz-container button,
.quiz-container-lg button {
    height: 10%;
    border-radius: 0 0 10px 10px;
    align-items: center;
    justify-content: center;
}

.quiz-container-lg .quiz-header {
    height: 93%;
}

.quiz-container-lg button {
    height: 7%;
}

.quiz-container h2,
.quiz-container-lg h2 {
    padding: .5rem;
    margin: 0;
    text-align: center;
    font-size: 1.3rem;
}


.quiz ul {
    list-style-type: none;
    text-align: left;

}

.quiz ul li:first-child {

    margin-top: 2%;
}

.quiz ul li {
    font-size: 1.2rem;
    padding: 1% 0;
}

.quiz ul li label {
    cursor: pointer;
}

.quiz button {
    background-color: green;
    color: #fff;
    border: none;
    display: block;
    width: 100%;
    cursor: pointer;
    font-size: 1rem;
    font-family: inherit;
}

.quiz button:hover {
    background-color: darkgreen;
}

.quiz button:focus {
    outline: none;
    background-color: mediumseagreen;
}

#quizSort {
    padding: 0;
    margin: 0 13%;

}

#quizSort li {
    border: 1px solid #fff;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    padding: 2% 0;
    margin-bottom: 2%;
}

#quizSort li:first-child {
    margin-top: 4%;
}

.quiz3-ol input {

    border-radius: 0.5rem;
}

.quiz3-ol li {
    padding-bottom: 5%;
    padding-left: 5%;
    text-align: left;
}

.quiz3-ol li:first-child {
    padding-top: 4%;
}

